import React, { Component } from 'react';
import '../stylesheets/Typing.css';
import downArrow from '../icon/down.svg';
import MediaQuery from 'react-responsive';
import bg from '../images/bg.png';
import Header from './Header';

class Typing extends Component {
  constructor(props) {
      super(props);
      this.state = {
        render: false
      };
    }

  componentWillMount = () => {
    setTimeout(() => {
      this.show();
      console.log('Now we wait...');
    }, this.props.wait)
  };

  handleClick = () => {
    for(let i = 0; i < window.innerHeight; i++){
      setTimeout(this.scroller(),10)
    };
  };

  scroller = () => {
    window.scrollTo({ top: window.innerHeight, behavior: 'smooth' });
  }

  show = () => {
    this.setState({render:true});
  }

  render(){
    const style = {
      main: {
        backgroundColor: '#e8efef',
        height: '120vh',
        width: '100vw',
        paddingTop: 50,
        // backgroundImage: "url(" + bg + ")", <-- bg img
        backgroundSize: "cover",
        backgroundRepeat: 'norepeat',
        scrollBehavior: "smooth"
      },
      dropText: {
        fontSize: '1.9vw',
        fontFamily: 'Monda',
        margin: 0,
        paddingTop: 3,
        paddingLeft: "26.5vw",
        color: '#2b2a2a'
      },
      arrow: {
        paddingTop: "8vw",
        textAlign: 'left',
        paddingLeft: "45.5vw"
      },
      about: {
        fontSize: 17,
        fontFamily: 'Monda',
        textAlign: 'center',
        paddingTop: 90,
        paddingBottom: 4,
        color: 'white'
      },
      mainMobile: {
        height: '90vw',
        width: window.innerWidth,
        backgroundColor: 'black',
        paddingTop: 20,
        backgroundImage: "url(" + bg + ")",
        backgroundSize: "cover",
        backgroundRepeat: 'norepeat'
      },
      dropTextMob: {
        fontSize: '3.3vw',
        fontFamily: 'Monda',
        margin: 0,
        paddingTop: 10,
        letterSpacing: 0.06,
        paddingLeft: 7,
        paddingBottom: 10,
        color: '#2b2a2a',
      },
      arrowMob: {
        textAlign: 'center',
        marginLeft: "41vw"
      },

      text1Mob: {
        fontSize: '16vw',
        fontFamily: 'Monda',
        marginTop: 15,
        paddingLeft: -window.innerWidth/10,
        color: '#2b2a2a',
      },

      text2Mob: {
        fontSize: '16vw',
        fontFamily: 'Monda',
        margin: 0,
        marginTop: 10,
        paddingLeft: -window.innerWidth/10,
        color: '#2b2a2a',
        letterSpacing: 0.01
      },

      myName: {
        fontSize: '7.5vw',
        fontFamily: 'Faustina',
        margin: 0,
        letterSpacing: "0.4vw",
        textAlign: 'center',
        marginLeft: "-4vw",
        paddingTop: '10vw',
        verticalAlign: 'middle',
        color: '#0c1a2d',
      }
    }


    return(
      <div >
        <MediaQuery query="(min-device-width: 650px)">
          <div style = {style.main} className = 'typewriter'>
            <div className = {this.hidden}>
              <h4 style = {style.myName}>Omer Khan</h4>
              <h3 style = {style.dropText}>Engineer | Web Developer | Passionate Learner  </h3>
              <div className = "lineBreak"></div>
            </div>
            <Header></Header>
            <div className = 'pageMove' style = {style.arrow}>
              <a href = "localhost:3000/#main">
                <img id = 'down' onClick = {() => this.handleClick()} src = {downArrow}/>
              </a>
            </div>
          </div>
        </MediaQuery>

        <MediaQuery query="(max-device-width: 650px)">
          <div style = {style.mainMobile} className = 'typewriterMob'>
            <div className = {this.hiddenMob}>
              <h1 style = {style.text1Mob}> Hi! <br/></h1>
              <h2 style = {style.text2Mob}> I&#39;m Omer</h2>
              <h3 style = {style.dropTextMob}>I&#39;m an engineering student and programming hobbyist</h3>
            </div>
              <a className = 'pageMoveMob' style = {style.arrowMob} href = "http://www.google.com">
              <img id = 'down' src = {downArrow}/>
              </a>
          </div>
        </MediaQuery>
      </div>
    );
  }
}
export default Typing;



