import React, { Component } from 'react';
import '../stylesheets/Header.css'
import {Button, Navbar, Nav, NavItem, NavDropdown, MenuItem, DropdownButton} from 'react-bootstrap';
import MediaQuery from 'react-responsive';
import Glyphicon from 'react-bootstrap/lib/Glyphicon';
import Gitlogo from '../icon/github-logo2.png';
import Linked from '../icon/linkedin-logo2.png';
import codepen from '../icon/codepen.png';

// import Grid from '@material-ui/core/Grid';
// import Media from 'react-media';

class Header extends Component {
  state = {
    show: false
  };

  handleClick = () => {
    console.log('They pressed on',this);
  };

  toggleOpen = () => {
    this.setState({dropdownOpen: !this.state.dropdownOpen});
    console.log('changed the state')};

  render () {
    const style = {
      mainDiv: {
        height: 'auto',
        opacity: 0.6,
        height: 60,
        paddingBottom: '50',
        overflow: 'visible',
        textAlign: 'center'

      },

      tags: {
        marginTop: -20,
        // marginRight: 14,
        // paddingLeft: '76vw',
        // top: 0,
        // right: 10,
      },

      mainIcon: {
        color: 'black'

      },

      NavHeader: {
        color: 'black',
        fontSize: '3vw',
        fontFamily: 'Monda',
        fontWeight: 'bold',
        // display:"flex",
        marginLeft: -8,
        marginBottom: -50,
        marginTop: -30,
        padding: 5,
        textDecoration: 'none',

      },

      MobileDrop: {
        align: 'center',
        textAlign: 'right',
        // paddingLeft: window.innerWidth - 290,
        paddingTop: 14,

        flexDirection:"Column",
        display:"flex"

      },

      Icon: {
        align: 'right',
        textAlign: 'right'
      },

      mainMobDiv:{

      },
      mobtags: {
        display: 'flex',
        position: 'fixed',
        marginLeft: '60vw'


      },
      mobIcon: {

      }
    }


    return (
        <div >

          {/*Non-Mobile devs*/}
          <MediaQuery query="(min-device-width: 650px)" style = { style.mainDiv } className = 'mainDiv'>
              {/*
              onClick={() => this.handleClick()
              */}

              <div style = {style.tags} id = 'tags'>
                    <a style = {style.Icon} href = "https://github.com/Meronkha " target="_blank">
                      <img  src = {Gitlogo} id = 'GitLogo'/>
                    </a>

                    <a style = {style.Icon} href = "https://www.linkedin.com/in/meronkha" target="_blank">
                      <img  src = {Linked} id = 'linkedLogo'/>
                    </a>

                    <a style = {style.Icon} href = "https://github.com/Meronkha " target="_blank">
                      <img  src = {codepen} id = 'codepen'/>
                    </a>


              </div>
          </MediaQuery>

          {/*Mobile devs*/}
          <MediaQuery query="(max-device-width: 650px)" style = { style.mainDiv } className = 'mainDiv'>

            <Navbar style = { style.mainMobDiv }>
            <Nav style={style.Nav} className = 'navBar-nav'>

            <Nav onClick={() => this.handleClick()}  pullRight>
            <NavItem eventKey={3} title= {<Glyphicon glyph="glyphicon glyphicon-user" />} style = {style.mainMobIcon} id="">

            </NavItem>
            </Nav>
            </Nav>

            </Navbar>
            <div style = {style.mobtags} id = 'tags'>
            <a style = {style.mobIcon} href = "https://github.com/Meronkha">
            <img  src = {Gitlogo} id = 'gitLogoMob'/>
            </a>

            <a style = {style.mobIcon} href = "https://www.linkedin.com/in/meronkha">
            <img  src = {Linked} id = 'linkedLogoMob'/>
            </a>


            </div>
          </MediaQuery>
        </div>
      );

  }
}

export default Header;



